import React, {Component} from 'react';
import {Card, CardBody, CardHeader, Col, Row, Button} from 'reactstrap';
import feathers from '../../feathers';

import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';

class Client extends Component {
  constructor (props) {
    super (props);
    this.state = {
      data: [],
      total: '',
    };
  }

  async componentWillMount () {
    var _this = this;

    let clientsResult = await feathers.service ('clients').find ({});

    console.log ('clientsResult 1', clientsResult);

    this.setState ({
      data: clientsResult.data,
    });
  }

  render () {
    var indexNumber = 0;

    const columns = [
      {
        dataField: 'nombre',
        text: 'Empresa',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },
      {
        dataField: 'email',
        text: 'Email',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },
      {
        dataField: 'telefono',
        text: 'Teléfono',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },
      {
        dataField: 'carga',
        text: 'Carga',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },
      {
        dataField: 'demandaContratada',
        text: 'Demanda Contratada',
        sort: true,
        formatter: (cell, row) => {
          return cell;
        },
      },

      {
        dataField: 'actions',
        text: 'Acciones',
        headerStyle: (colum, colIndex) => {
          return {width: 150};
        },
        formatter: (cell, row) => {
          return (
            <Row>
              <Col col="6" sm="4" md="2" xl className="mb-3 mb-xl-0">
                <Button
                  color="danger"
                  className="btn-square pull-right"
                  style={{marginLeft: 5}}
                >
                  <i className="fa fa-trash" />
                </Button>{'  '}
                <Button
                  color="success"
                  className="btn-square pull-right"
                  onClick={() =>
                    this.props.history.push ('/clientes/' + row._id)}
                >
                  <i className="fa fa-pencil" /> Editar
                </Button>
              </Col>
            </Row>
          );
        },
      },
    ];

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" lg="12">
            <Card>
              <CardHeader>
                Administración - Clientes
                <div className="card-header-actions">
                  <i className="fa fa-download" />
                  <a href="http://ipco.com" className="card-header-action">
                    <small className="text-muted">EXPORTAR</small>
                  </a>
                </div>
              </CardHeader>
              <Row
                className="pull-right"
                style={{
                  marginTop: '5px',
                  paddingRight: '5px',
                }}
              >
                <Col col="12" sm="12" md="12">
                  <Button
                    onClick={() => this.props.history.push ('/clientes/form')}
                    color="success"
                    className="btn-square pull-right"
                  >
                    <i className="fa fa-plus" />{' '}
                    Agregar Cliente
                  </Button>
                </Col>
              </Row>
              <Row>
                <CardBody style={{paddingTop: 8}}>
                  <BootstrapTable
                    keyField="_id"
                    data={this.state.data}
                    columns={columns}
                    pagination={paginationFactory ()}
                    bootstrap4
                    condensed
                    striped
                    hover
                  />
                </CardBody>
              </Row>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Client;
